.mainhead {
  gap: 0.5rem;
}
.mainhead .searchbar {
  display: flex;
  align-items: center;
}
.mainhead .searchbar input {
  height: 2.5rem;
  padding: 0.25em 0.75em;
  width: clamp(100px, 40vw, 300px);
  font-weight: bold;
  font-size: 1.125rem;
  border: 1px solid grey;
  outline: 1px solid purple;
}
.mainhead .searchbar span {
  padding: 0.7rem;
  background-color: purple;
}
.mainhead .usericon {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 600px) {
  .mainhead .usericon {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
}

.mainnav a {
  text-decoration: none;
  color: #ccc;
}
.mainnav li {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.mainnav li:hover {
  color: #222;
}
@media (max-width: 450px) {
  .mainnav span {
    display: none;
  }
}

.container span {
  color: #262626;
}

.poptestimony {
  animation: down-up 60s ease-out infinite;
  width: 20rem;
  padding: 0.25em 0.75rem;
  border: 2px solid purple;
  background: #00001033;
  position: fixed;
  top: 10rem;
  margin-left: 2rem;
}
.poptestimony p {
  line-height: 1.5;
  font-weight: 500;
}
.poptestimony p span {
  font-weight: bolder;
}

@keyframes down-up {
  0% {
    top: 80%;
    opacity: 1;
  }
  17% {
    top: 20%;
    opacity: 1;
  }
  19% {
    top: -10%;
    opacity: 0.2;
  }
  100% {
    top: -10%;
  }
}
.livechat .livediv {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 3px 4px rgba(255, 253, 253, 0.1);
  backdrop-filter: blur(2px);
  height: 80vh;
  width: clamp(300px, 80vw, 400px);
  position: fixed;
  bottom: 10%;
  right: 5%;
}
.livechat .livehead div {
  display: flex;
  grid-gap: 5px;
}
.livechat .livehead div * {
  font-size: 1.5rem;
}

a {
  text-decoration: none;
}

.popcard {
  background: #eee;
  border-radius: 1rem;
  padding: 1rem;
  color: purple;
  position: fixed;
  z-index: 10;
  margin-block: auto;
  line-height: 1.5;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  display: none;
}
.popcard header {
  text-align: center;
  color: #570057;
  font-size: 2rem;
  font-weight: 900;
}
.popcard p {
  line-height: 2.5;
}
.popcard button {
  padding: 0.5em 1.125em;
  margin: 1rem;
  outline: none;
  border: none;
  width: fit-content;
  align-self: end;
  font-weight: 900;
  background: #ff1aff;
  color: #1a001a;
}

header.header {
  position: sticky;
  top: -1px;
  z-index: 3;
  background: black;
  padding: 0 1em;
  display: block;
  color: white;
}
header.header h1 {
  font-size: 3rem;
}
header.header span {
  color: #262626;
}
header.header img {
  display: block;
  width: 3rem;
  border: 2px solid #262626;
  border-radius: 6px;
  transition: all 0.45s ease-in-out;
}
header.header div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
header.header nav {
  margin-left: auto;
  display: none;
}
header.header ul {
  display: flex;
  line-height: 2;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
header.header ul a {
  color: #ddd;
}
@media (min-width: 600px) {
  header.header {
    display: flex;
  }
  header.header img {
    display: none;
  }
  header.header nav {
    display: grid;
    place-content: center;
  }
  header.header ul {
    flex-direction: row;
    gap: 1rem;
  }
}

.head-banner {
