.services {
  position: relative;
  text-align: center;
  background: black;
  color: white;
  line-height: 2;
  padding: 3rem 1rem 1rem 2rem;
}
.services h2 {
  font-size: 2rem;
}
.services hr {
  border: 5px solid #262626;
}
.services ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.services li {
  padding: 1rem;
}
.services .side-attract {
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(-50%);
  background: #262626;
  height: 100px;
  width: clamp(2.5rem, 5vw, 100px);
  display: grid;
  place-content: center;
}
.services .side-attract ul {
  display: grid;
  gap: 0.75rem;
  grid-template-columns: repeat(3, 1fr);
  height: 100%;
  width: 100%;
  transform: translateX(50%);
}
.services .side-attract ul div {
  background: white;
  border-radius: 50%;
  padding: clamp(0.2rem, 2.5vw, 0.5rem);
  display: grid;
  place-content: center;
  overflow: hidden;
}

.about {
  padding: 1rem;
  line-height: 2;
  background-size: cover;
  background-position: center;
}
.about h2 {
  text-align: center;
}
.about div {
  display: flex;
  justify-content: end;
}
.about p {
  width: 60%;
  text-align: end;
}

.testimonies h2 {
  text-align: center;
}
.testimonies img {
  width: 4rem;
  border-radius: 50%;
}
.testimonies ul {
  display: grid;
  gap: 1rem;
  padding: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
.testimonies li {
  background: white;
  border-radius: 1rem;
  line-height: 2;
  display: flex;
  align-items: center;
  padding: 1rem;
  color: black;
}
.testimonies p + p {
  color: #aaa;
}
.testimonies div {
  color: black;
}

.awards {
  background: #00001a;
  padding: 2rem;
}
.awards ul {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.awards ul li {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.awards ul img {
  width: 7rem;
  height: 14rem;
}

.faq {
  display: grid;
  place-content: center;
  padding: 1rem;
}
.faq .container {
  max-width: 700px;
}
.faq h3 {
  text-align: center;
}
.faq details {
  border: 1px solid #e600e6;
  margin: 1rem 0;
  line-height: 2;
}
.faq details p {
  padding: 1rem;
}
.faq details summary {
  background: linear-gradient(120deg, var(#262626), var(#e600e6));
}

.contact {
  line-height: 2;
}
.contact h3 {
  text-align: center;
}
.contact img {
  width: 2rem;
}
.contact input {
  border: none;
  outline: none;
  border-bottom: 2px solid #262626;
  color: #e600e6;
  background-color: rgba(0, 0, 0, 0);
}
.contact main {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 1rem;
}
.contact form {
  display: grid;
  gap: 1rem;
  grid-auto-columns: 1fr;
  place-items: center;
  grid-template-areas: "name email" "subject subject" "message message" "button button";
}
.contact form input {
  width: 100%;
  min-height: 2rem;
  padding: 0.5em;
}
.contact form input[name=name] {
  grid-area: name;
}
.contact form input[name=email] {
  grid-area: email;
}
.contact form input[name=subject] {
  grid-area: subject;
}
.contact form textarea {
  grid-area: message;
  width: 100%;
  padding: 0.5em;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #262626;
}
.contact form button {
  grid-area: button;
  width: max-content;
  padding: 0.5em 0.75em;
  background: linear-gradient(120deg, var(#262626), var(#e600e6));
  border: none;
  border-radius: 5px;
  outline: none;
}
.contact ul {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contact ul li {
  display: flex;
  gap: 1rem;
}
.contact ul div {
  display: flex;
  flex-direction: column;
  border-left: 1px solid #262626;
  padding-left: 1rem;
}
@media (max-width: 700px) {
  .contact main {
    grid-template-columns: 1fr;
  }
}

footer.footer {
  padding: 1rem;
  overflow: hidden;
  line-height: 1.5;
  color: white;
  display: grid;
  place-content: center;
}
footer.footer .logo {
  text-align: center;
}
footer.footer .container {
  display: grid;
  gap: 1rem;
  justify-content: space-between;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}
footer.footer a {
  color: white;
}
footer.footer a:hover {
  color: #555;
}

