
.mainhead
  gap: .5rem
  .searchbar
    display: flex
    align-items: center


    input
      height: 2.5rem
      padding: .25em .75em
      width: clamp(100px, 40vw, 300px )
      font-weight: bold
      font-size: 1.125rem
      border: 1px solid grey
      outline: 1px solid purple
      

    span
      padding: .7rem
      background-color: purple

  .usericon
    display: flex
    align-items: center
    gap: 1rem

  @media ( max-width: 600px )
    .searchbar
      input
        // height: 2rem
        // padding: .2rem .7rem
        

      span
        // padding: .43rem

    .usericon
      display: flex
      align-items: center
      gap: .5rem



  
.mainnav
  a
    text-decoration: none
    color: #ccc
  li
    display: flex
    align-items: center
    gap: 1rem

    &:hover
      color: #222

  @media ( max-width: 450px )
      span
        display: none

.container
  div

  span
    color: #262626
    
.poptestimony
  animation: down-up 60s ease-out infinite
  width: 20rem
  padding: .25em .75rem
  border: 2px solid purple
  background: #00001033
  position: fixed
  top: 10rem
  margin-left: 2rem


  p
    line-height: 1.5
    font-weight: 500

    span
      font-weight: bolder

@keyframes down-up
  0%
    top: 80%
    opacity: 1

  17%
    top: 20%
    opacity: 1


  19%
    top: -10%
    opacity: .2

  100%
    top: -10%

.livechat
  .livediv
    background-color: rgba(255, 255, 255, 0.1)
    border-radius: 10px
    border: 1px solid rgba(255, 255, 255, .5)
    box-shadow: 0 0 3px 4px rgba(255, 253, 253, 0.1)
    backdrop-filter: blur(2px)
    height: 80vh
    width: clamp(300px, 80vw, 400px)

    position: fixed
    bottom: 10%
    right: 5%
  
  .livehead

    div
      display: flex
      grid-gap: 5px
      *
        font-size: 1.5rem



