a
  text-decoration: none
.popcard
  background: #eee
  border-radius: 1rem
  padding: 1rem
  color: purple
  position: fixed
  z-index: 10
  margin-block: auto
  line-height: 1.5
  max-width: 500px
  display: flex
  flex-direction: column
  display: none
 
  header
    text-align: center
    color: darken(purple, 8% )
    font-size: 2rem
    font-weight: 900
  p
    line-height: 2.5
  button
    padding: .5em 1.125em
    margin: 1rem
    outline: none
    border: none
    width: fit-content
    align-self: end
    font-weight: 900
    background: lighten(purple, 30% )
    color: darken(purple, 20% )

header.header
    position: sticky
    top: -1px
    z-index: 3
    background: black
    padding: 0 1em
    display: block
    color: white

    h1
        font-size: 3rem

    span 
        color: $primary-color

    img
        display: block
        width: 3rem
        border: 2px solid $primary-color
        border-radius: 6px
        transition: all 0.45s ease-in-out

    div
        display: flex
        flex-direction: row
        justify-content: space-between

    nav
        margin-left: auto
        display: none

    ul
        display: flex
        line-height: 2
        flex-direction: column
        justify-content: center
        text-align: center

        a   
            color: #ddd

    @media (min-width: 600px)
        display: flex

        img
            display: none

        nav
            display: grid
            place-content: center

        ul
            flex-direction: row
            gap: 1rem

