.services
    position: relative
    text-align: center
    background: black
    color: white
    line-height: 2
    padding: 3rem 1rem 1rem 2rem

    h2
        font-size: 2rem
        

    hr
        border: 5px solid $primary-color

    ul
        display: grid
        grid-template-columns: repeat(2, 1fr)

    li
        padding: 1rem

    .side-attract
        position: absolute
        left: 0
        top: 0
        transform: translateY(-50%)
        background: $primary-color
        height: 100px
        width: clamp(2.5rem, 5vw, 100px)
        display: grid
        place-content: center

        ul
            display: grid
            gap: .75rem
            grid-template-columns: repeat(3, 1fr)
            height: 100%
            width: 100%
            transform: translateX(50%)

            div
                background: white
                border-radius: 50%
                padding: clamp(.2rem, 2.5vw, .5rem)
                
                display: grid
                place-content: center
                overflow: hidden

.about
    padding: 1rem
    line-height: 2
    background-size: cover
    background-position: center
    // background-blend-mode: add

    h2
        text-align: center

    div
        display: flex
        justify-content: end

    p
        width: 60%
        text-align: end

.testimonies

    h2
        text-align: center

    img
        width: 4rem
        border-radius: 50%

    ul
        display: grid
        gap: 1rem
        padding: 1rem
        grid-template-columns: repeat(auto-fit, minmax(350px,1fr))

    li
        background: white
        border-radius: 1rem
        line-height: 2
        display: flex
        align-items: center                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                     
        padding: 1rem
        color: black

    p + p 
        color: #aaa

        

    div
        color: black


.awards
    background: lighten($bg-color, 2%)
    padding: 2rem

    ul
        display: grid
        gap: 1rem
        grid-template-columns: repeat(2, 1fr)

        li
            display: flex
            flex-direction: column
            align-items: center

        img
            width: 7rem
            height: 14rem


.faq
    display: grid
    place-content: center
    padding: 1rem

    .container
        max-width: 700px

    h3
        text-align: center

    details
        border: 1px solid $secondary-color
        margin: 1rem 0
        line-height: 2

        p
            padding: 1rem

        summary
            background: $gradient


.contact
    line-height: 2
    // 

    h3
        text-align: center

    img
        width: 2rem

    input
        border: none
        outline: none
        border-bottom: 2px solid $primary-color
        color: $secondary-color
        background-color: rgba(0, 0, 0, 0)

    main 
        display: grid
        gap: 2rem
        grid-template-columns: repeat(2, 1fr)
        padding: 1rem

    form
        display: grid
        gap: 1rem
        grid-auto-columns: 1fr
        place-items: center
        grid-template-areas: "name email" "subject subject" "message message" "button button"

        input
             width: 100%
             min-height: 2rem
             padding: .5em


        input[name = "name"]
             grid-area: name

        input[name = "email"]
             grid-area: email 
             

        input[name = "subject"]
             grid-area: subject
             
            
        textarea
            grid-area: message
            width: 100%
            padding: .5em
            background-color: rgba(0, 0, 0, 0)
            border: 1px solid $primary-color

        button
            grid-area: button
            width: max-content
            padding: .5em .75em
            background: $gradient
            border: none
            border-radius: 5px
            outline: none


    ul
        min-height: 100%
        display: flex
        flex-direction: column
        justify-content: space-between

        li
            display: flex
            gap: 1rem

        div
            display: flex
            flex-direction: column
            border-left: 1px solid $primary-color
            padding-left: 1rem

    @media (max-width: 700px)
        main
            grid-template-columns: 1fr


footer.footer
    padding: 1rem
    overflow: hidden
    line-height: 1.5
    color: white
    display: grid
    place-content: center

    .logo
        text-align: center

    .container
        // max-width: 700px
        display: grid
        gap: 1rem
        justify-content: space-between
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr))

    a
        color: white

        &:hover
            color: #555
         